<template>
  <v-container>
    <v-card :loading="l" :disabled="l" outlined>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-text-field
        v-model="search"
          dense

          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
      </v-card-title>
      <v-data-table
    :headers="headers"
    :items="todo"
    :search="search"
    :items-per-page="5"
    class="elevation-1"
    >


    <template v-slot:item.tipo="{ item }">
    
        {{ (item.tipo) }} 
    
    </template>

    <template v-slot:item.tamanho="{ item }">
      <v-chip color="primary">
        {{ formatToByte(item.tamanho) }}
      </v-chip>
    
    </template>

    <template v-slot:item.datacad="{ item }">
    {{ formatDate(item.datacad) }} 
    
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon
      small
      class="mr-2"
      @click="downloadItem(item)"
    >
      mdi-download
    </v-icon>

    <v-icon
    small
    @click="deleteItem(item)"
  >
    mdi-delete
  </v-icon>
    
    </template>
  
  
  
  </v-data-table>
  </v-card>
    <v-snackbar
    v-model="snackbar"
  >
    {{ text }}

    <template v-slot:action="{ attrs }">
      <v-btn color="pink" text v-bind="attrs" @click="snackbar = false">
        fechar
      </v-btn>
    </template>
  </v-snackbar>

   </v-container>
</template>
<script>

import { mapGetters,mapActions } from 'vuex'
import { formatToByte,formatDate } from '@/libs/format.js'

export default {
    data() {
        return {
            count:0,
            snackbar: false,
            text: `Novos Dados de carregamento salvo`,
            search:'',
            headers: [
          {
            text: '#',
            align: 'start',
            sortable: false,
            value: 'id_carregamento',
          },
          { text: 'nome', value: 'nome' },
          { text: 'tipo', value: 'tipo' },
          { text: 'tamanho', value: 'tamanho' },
          { text: 'data', value: 'datacad' },
          { text: 'ao', value: 'action' },
        ],
        }
    },
    computed: {
        ...mapGetters({
            todo:'carregamento/todo',
            l:'carregamento/loading_state',

        })
    },
    watch: {
        todo:{

          
          handler(oldVal, val){

              this.call({text:`Tabela atualizado`})

          },
            immediate:false
        }
    },
    methods: {
      formatToByte,
      formatDate,
      downloadItem(item){
        try {

          this.downloadFileUploaded(item)

          this.call({text:`Descarregando!`})
          
        } catch (error) {
          this.call({color:'#e14343',text:error})
          
        }
      },
      async deleteItem(item){
        try {
          
          (await this.delete(item))

          this.call({text:`${item.nome} foi apagado!`})
        
        } catch (error) {

          this.call({color:'#e14343',text:error})
        }

      },
        ...mapActions({
            delete:'carregamento/deleteUploadHistory',
            downloadFileUploaded:'carregamento/downloadFileUploaded',
            get:'carregamento/getUploadHistoryByIdUsuario',
            call:'assets/notificate',

        }),
    },
    async created() {
 
        await this.get()
  
    },
    
}
</script>